.input100 {
  color: #555;
  width: 100%;
  height: 50px;
  -o-transition: all .4s;
  background: none;
  padding: 0 10px 0 80px;
  font-family: Ubuntu-Regular;
  font-size: 20px;
  line-height: 1.2;
  transition: all .4s;
  display: block;
}

.focus-input100 {
  width: 100%;
  height: 100%;
  pointer-events: none;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.focus-input100:before {
  content: "";
  width: 0;
  height: 1px;
  -o-transition: all .4s;
  background: -moz-linear-gradient(left, #a445b2, #d41872, #fa4299);
  background: linear-gradient(left, #a445b2, #d41872, #fa4299);
  background: -webkit-linear-gradient(left, #a445b2, #d41872, #fa4299);
  background: -o-linear-gradient(left, #a445b2, #d41872, #fa4299);
  transition: all .4s;
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.focus-input100:after {
  color: #999;
  content: attr(data-placeholder);
  width: 100%;
  -o-transition: all .4s;
  font-family: Linearicons-Free;
  font-size: 18px;
  transition: all .4s;
  display: block;
  position: absolute;
  top: 40px;
  left: 35px;
}

.input100:focus {
  padding-left: 60px;
}

.input100:focus + .focus-input100:after {
  color: #d41872;
  left: 23px;
}

.input100:focus + .focus-input100:before {
  width: 100%;
}

.has-val.input100 + .focus-input100:after {
  color: #d41872;
  left: 23px;
}

.has-val.input100 + .focus-input100:before {
  width: 100%;
}

.has-val.input100 {
  padding-left: 60px;
}

.container-login100-form-btn {
