.validate-input {
  position: relative;
}

.alert-validate:before {
  content: attr(data-validate);
  max-width: 70%;
  pointer-events: none;
  color: #c80000;
  text-align: left;
  visibility: hidden;
  opacity: 0;
  -o-transition: opacity .4s;
  background-color: #fff;
  border: 1px solid #c80000;
  border-radius: 2px;
  padding: 4px 25px 4px 10px;
  font-family: Ubuntu-Bold;
  font-size: 13px;
  line-height: 1.4;
  transition: opacity .4s;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.alert-validate:after {
  content: "ï„ª";
  color: #c80000;
  font-family: FontAwesome;
  font-size: 16px;
  display: block;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

.alert-validate:hover:before {
  visibility: visible;
  opacity: 1;
}

@media (max-width: 992px) {
  .alert-validate:before {
    visibility: visible;
    opacity: 1;
  }
}

